::-webkit-scrollbar {
    width: 12px;
}
 
::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3); 
    border-radius: 10px;
}
 
::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5); 
}

.button{
  padding: 10px;
  border-radius: 10px;
  background-color: aquamarine;
  border: none;
  color:white;
  transition: all 0.5s;
}

.block{
  display: flex;
  justify-content: center;
  min-height: 100vh;
  align-items: center;
}

.button:hover{
  transform: scale(1.3);
  background-color:cyan;
}

.pakemonList{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.pakemom{
  border: 2px solid black;
  padding: 30px;
  margin: 15px 0px;
  border-radius: 20px;
  transition: all 0.7s;
}

.pakemom:hover{
  border: 2px solid red;
  transform: rotate(360deg);
}

.pakemon-text{
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  font-size: large;
  text-align: center;
}